<template>
  <!--订单-->
  <div>
    <section class="order">
      <!-- <header class="header">
        <a class="header_title">
          <span class="header_title_text">订单列表</span>
        </a>
      </header> -->
      
      <!-- //*公共header组件 -->
      <HeaderTop title="订单列表"/>

      <section class="order_no_login">
        <img src="./images/order/person.png">
        <h3>登录后查看外卖订单</h3>
        <router-link to="/login" class="header_login_text">
          <button>立即登陆</button>
        </router-link>
      </section>
    </section>
  </div>
</template>

<script>
//* 引入公共组件HeaderTop
import HeaderTop from '../../components/HeaderTop/HeaderTop'

export default {
  components: {
    HeaderTop
  }
}
</script>

<style lang='stylus' rel='stylesheet/stylus'>

.order { // 订单
  width: 100%;

  .order_no_login {
    padding-top: 140px;
    width: 60%;
    margin: 0 auto;
    text-align: center;

    >img {
      display: block;
      width: 100%;
      height: 30%;
    }

    >h3 {
      padding: 10px 0;
      font-size: 17px;
      color: #6a6a6a;
    }

    button {
      display: inline-block;
      background: #02a774;
      font-size: 14px;
      color: #fff;
      border: 0;
      outline: none;
      border-radius: 5px;
      padding: 10px 20px;
    }
  }
}
</style>



